import React from "react";
import s from "./Post.module.css";

const Post = (props) => {
  return (
    <div className={s.item}>
      <img alt={"Post"} src="data:image/jpeg;base64," />
      {props.message}
    <button className={s.buttonLike}>
      {props.likeCount}
      </button>
    </div>
  );
};

export default Post;